.tab-container {
  border: 1px solid #eeeeee;
  width: 100%;
  background: #fff;
}
.tab-container nav {
    font-size: 18px;
    color: #353535;
    border-bottom: 1px solid #eeeeee;
    font-weight: bold;
}
.tab-container nav div {
      height: 50px;
      line-height: 50px;
      float: left;
      padding: 0 16px;
      cursor: pointer;
      border-top: 4px solid #fff;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      border-left: 1px solid #fff;
      border-right: 1px solid #fff;
}
.tab-container nav div.active {
        color: #ff5e2d;
        border-top: 4px solid #ff5e2d;
        border-left: 1px solid #eeeeee;
        border-right: 1px solid #eeeeee;
        position: relative;
}
.tab-container nav div.active::after {
          content: '';
          display: inline-block;
          position: absolute;
          bottom: -1px;
          left: 0;
          width: 100%;
          height: 2px;
          background: #fff;
}
.tab-container .content {
    padding: 16px;
}
.tab-container .content img {
      width: 100%;
}

.emoji {
