  font-size: .533333rem/* 40/75 */
  ;
  margin: 0;
  font-weight: normal;
}
.small {
  font-size: 12px;
  -webkit-transform: scale(.9);
          transform: scale(.9);
}
.single-text {
  overflow: hidden;
  /* white-space: nowrap; */
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}
ul,
ol {
  list-style: none;
}
img {
  vertical-align: middle;
  outline: none;
  width: 100%;
}
input,
textarea {
  border: none;
  outline: none;
  -webkit-appearance: none;
}
.clearfix::after,
.clearfix::before {
  content: '';
  width: 0;
  height: 0;
  visibility: hidden;
  display: table;
}
.clearfix::after {
  clear: both;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
a {
  text-decoration: none;
  color: inherit;
  -webkit-tap-highlight-color: transparent;
}
a:active {
  background-color: transparent;
}
textarea:focus {
  outline: none;
}
audio {
  /* opacity: 0;
  display: block !important; */
}
